  h1, h2, h3, h4, h5, h6 {
    color: #444444;
  }
  
  .bg-color {
    background-color: rgb(101, 114, 133);
  }

  .nav-user-img {
    height: 40px;
    width: 40px;
  }
  
  .content-section {
    background: #ffffff;
    padding: 10px 20px 20px 20px;
    border: 1px solid #dddddd;
    border-radius: 3px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  
  .article-title {
    color: #444444;
  }
  
  a.article-title:hover {
    color: #428bca;
    text-decoration: none;
  }
  
  .article-content {
    white-space: pre-line;
  }
  
  .article-img {
    height: 65px;
    width: 65px;
    margin-right: 16px;
  }
  
  .article-metadata {
    padding-bottom: 1px;
    margin-bottom: 4px;
    border-bottom: 1px solid #e3e3e3
  }
  
  .article-metadata a:hover {
    color: #333;
    text-decoration: none;
  }
  
  .article-svg {
    width: 25px;
    height: 25px;
    vertical-align: middle;
  }
  
  .account-img {
    height: 125px;
    width: 125px;
    margin-right: 20px;
    margin-bottom: 16px;
  }
  
  .account-heading {
    font-size: 2.5rem;
  }

  .scrolling {
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
  }

  .alert {
    margin-top: 20px;
  }